import store from '../store/index';
import { Message } from 'element-ui';


/**/
export function initReg(url,params,val){
    const urls = url.split('/');//
    const rule = store.state.regRule;//
    const validation = rule[urls[0]][urls[1]][params].validation;//
    const name = rule[urls[0]][urls[1]][params].name;//
    const description = rule[urls[0]][urls[1]][params].description;//
    const regRule = eval(validation.replace('regex:',''));
    if(!regRule.test(val)){
        Message.error(name+':'+description);
    }
    return regRule.test(val);
  }


