/**axios
 * 
 */
import axios from 'axios'; // axios
import QS from 'qs'; // qspost
// vanttoastui
import { Message } from 'element-ui';
import httpInit from './httpInit';
import { showLoading, hideLoading } from '../assets/loading';
import store from '../store/index'
import { log } from 'util';



/*aes url*/
// var httpUrl = httpInit.initHttp();
var baseURL = '';
// httpUrl.then((res) => {
//   // baseURL = res;
//   // console.log(res);
  
// }, (err) => {
//   console.log(err)
// })
// console.log(window.location.host);

// 
if (process.env.NODE_ENV == 'development') {    
    axios.defaults.baseURL = process.env.API_ROOT + '/Api/';
    baseURL = axios.defaults.baseURL;
} else if (process.env.NODE_ENV == 'production') {    
    axios.defaults.baseURL = '' + 'Api/';
    baseURL = axios.defaults.baseURL;
}


// 
axios.defaults.timeout = 10000;

// post
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
axios.defaults.withCredentials = true;

// 
axios.interceptors.request.use(
  config => {
    if (!config.data.includes('versionName')) {
      config.headers.Accept = 'application/x.LegendAgent.MemberAppV001+json';
    }
    // tokenhttpheadertoken
    // tokentoken
    const token = localStorage.getItem("token");
    token && (config.headers.Authorization = 'Bearer ' + token);

    showLoading();
    return config;
  },
  error => {
    return Promise.error(error);
  })

// 
axios.interceptors.response.use(data => {
  const code = data.data.code;
  if (code == 1001) { //
    // this.$store.commit("lmodelShow", true);
    Message.error('')
  } else if (code == 1003) {
    Message.error(Object.values(data.data.message)[0]);
  } else if (code == 1) {
    Message.error(data.data.message);
  }
  hideLoading();
  return Promise.resolve(data);
}, error => {
  
  hideLoading();
  Message.error({
    message: ','
  })
  return Promise.reject(error)
})

/** 
 * getget 
 * @param {String} url [url] 
 * @param {Object} params [] 
 */
export function get(url, params) {
  return new Promise((resolve, reject) => {
    axios.get(url, {
      params: params
    })
      .then(res => {
        resolve(res.data);
      })
      .catch(err => {
        reject(err.data)
      })
  });
}
/** 
 * postpost 
 * @param {String} url [url] 
 * @param {Object} params [] 
 */
export function post(url, params) {
  return new Promise((resolve, reject) => {
    axios.post(url, QS.stringify(params))
      .then(res => {
        resolve(res.data);
      })
      .catch(err => {
        reject(err.data)
      })
  });
}

export { baseURL }



